import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import "./TopViewReport.css";
// ShortReport.css   css    TopViewReport.css  .

const TopViewReport = (props) => {
  const [reportList, setReportList] = useState([]);
  const navigate = useNavigate();

  useEffect(() => {
    async function fetchData() {
      try {
        await axios
          .get("/api/book-reports", { params: { sort: "date" } })
          .then((res) => {
            setReportList(res.data.data || []);
          });
      } catch (err) {
        console.log(err);
      }
    }
    fetchData();
  }, []);

  return (
    <div className="Report-area">
      {reportList.length
        ? reportList.slice(0, 10).map((data, index) => {
            //  10 
            return (
              <div
                className="report-box"
                key={index}
                onClick={() => {
                  navigate("/ViewReportPage", {
                    state: { isbn: data.isbn, userid: data.userid },
                  });
                }}
              >
                <div className="title">
                  <p className="centerBookTitle">{data.bookTitle}</p>
                  <p className="centerReportTitle">{data.title}</p>
                  <p className="nickName">
                    {data.rating} | {data.userid}
                  </p>
                  <p className="date">{data.date}</p>
                </div>
                <div className="bookContent">
                  <img src={data.thumbnail} alt="thumbnail" />
                </div>
              </div>
            );
          })
        : "  ."}
    </div>
  );
};

export default TopViewReport;
