import React from 'react';
import { useNavigate } from 'react-router-dom'
import './Nav.css'

const Nav = () => {
  const navigate = useNavigate();

  const path = window.location.pathname;
  if (path === '/' || path === '/SignUp') return null; /*,   Nav  */

  return (
    <div className="nav-area">
      <div style={{ fontWeight: path === '/AllReport' && 'bold' }} onClick={() => navigate('/AllReport')}> </div>
      <div style={{ fontWeight: path === '/RecommendPage' && 'bold' }} onClick={() => navigate('/RecommendPage')}> </div>
      <div style={{ fontWeight: path === '/MyBookPage' && 'bold' }} onClick={() => navigate('/MyBookPage')}> </div>
    </div>
  )
};

export default Nav;
