import React, { useState, useEffect } from "react";
import { TextField } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import styled from "styled-components"; //CSS-IN_JS
import axios from "axios";
import { useLocation } from "react-router-dom";

const Wrapper = styled.div`
  width: 70rem;
  margin: 2rem auto;
  border-radius: 4px;
  background-color: var(--white-color);
  padding: 0.5rem 0.5rem 2rem;
  overflow: hidden;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
`;

const Spacing = styled.div`
  width: 100%;
  height: 10px;
`;

const useStyles = makeStyles((theme) => ({
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(1),
      width: "25ch",
    },
  },
}));

//isbn userid      
const ViewReportPage = () => {
  const classes = useStyles();
  const { state } = useLocation(); //isbn userid 

  const [bookInfo, setBookInfo] = useState({
    authors: "",
    isbn: "",
    publisher: "",
    thumbnail: "",
    bookTitle: "",
  });

  const [ReportInfo, setReportInfo] = useState({
    contents: "",
    isbn: "",
    rating: "",
    title: "",
    views: "",
    date: "",
    userid: "",
  });

  const onSetBookInfo = (data) => {
    setBookInfo({
      authors: data.authors,
      isbn: data.isbn,
      publisher: data.publisher,
      thumbnail: data.thumbnail,
      bookTitle: data.title,
    });
  };

  const onSetReportInfo = (data) => {
    setReportInfo({
      contents: data.contents,
      isbn: data.isbn,
      rating: data.rating,
      title: data.title,
      views: data.views,
      date: data.date,
      userid: data.userid,
    });
  };

  //isbn, userid   
  useEffect(() => {
    async function fetchData() {
      try {
        await axios
          .get("/api/book-reports/" + state.isbn + "/" + state.userid)
          .then((res) => {
            return res.data;
          })
          .then((data) => {
            onSetReportInfo(data);
          });
      } catch (err) {
        console.log(err);
      }
    }
    fetchData();
  }, [state.isbn, state.userid]);

  const bookIsbn = state.isbn;

  //isbn   
  useEffect(() => {
    async function fetchData() {
      try {
        await axios
          .get("/api/books/" + bookIsbn)
          .then((res) => {
            return res.data;
          })
          .then((data) => {
            onSetBookInfo(data);
          });
      } catch (err) {
        console.log(err);
      }
    }
    fetchData();
  }, [bookIsbn]);

  const dateFormat = ReportInfo.date;

  return (
    <Wrapper>
      <h3> </h3>
      <form className={classes.root} noValidate autoComplete="off">
        <div>
          <TextField
            id="filled-read-only-input"
            label=" "
            style={{ width: "57%" }}
            value={bookInfo.bookTitle}
            InputProps={{
              readOnly: true,
            }}
            variant="filled"
          />
          <TextField
            id="filled-read-only-input"
            label=""
            value={bookInfo.authors}
            InputProps={{
              readOnly: true,
            }}
            variant="filled"
          />
          <TextField
            id="filled-read-only-input"
            label=""
            value={bookInfo.publisher}
            InputProps={{
              readOnly: true,
            }}
            variant="filled"
          />
        </div>
        <h3>  </h3>
        <div>
          <TextField
            id="outlined-basic"
            label=" "
            style={{ width: "98%" }}
            value={ReportInfo.title}
            variant="outlined"
          />
          <TextField
            id="outlined-basic"
            label=""
            value={ReportInfo.userid}
            variant="outlined"
          />
          <TextField
            id="outlined-basic"
            label=" "
            value={dateFormat}
            variant="outlined"
          />
          <TextField
            id="outlined-number"
            label="(1~10)"
            type="number"
            value={ReportInfo.rating}
            InputProps={{
              readOnly: true,
            }}
            InputLabelProps={{
              shrink: true,
            }}
            variant="outlined"
          />
          <TextField
            id="outlined-basic"
            label=""
            value={ReportInfo.views}
            variant="outlined"
          />
        </div>
        <h3></h3>
        <div>
          <TextField
            id="outlined-basic"
            style={{ width: "98%" }}
            InputProps={{
              readOnly: true,
            }}
            multiline
            placeholder="  "
            value={ReportInfo.contents}
            variant="outlined"
          />
        </div>
      </form>
      <Spacing />
    </Wrapper>
  );
};

export default ViewReportPage;
