import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "@material-ui/core";
import { useCookies } from "react-cookie";
import TextField from "@material-ui/core/TextField";
import GroupAddIcon from "@material-ui/icons/GroupAdd";
import axios from "axios";
import "./SignIn.css";

const SignIn = () => {
  const navigate = useNavigate();
  // eslint-disable-next-line
  const [setCookie] = useCookies(["user"]);

  const [id, setId] = useState("");
  const [pw, setPw] = useState("");

  const onChangeId = (e) => {
    setId(e.target.value);
  };
  const onChangePw = (e) => {
    setPw(e.target.value);
  };

  const onClickButton = () => {
    if (id === "") {
      alert(" .");
    } else if (pw === "") {
      alert(" ");
    } else {
      axios
        .post("/api/auth", {
          userid: id,
          password: pw,
        })
        .then((res) => {
          if (res.data.issuccess) {
            setCookie(
              "user",
              { userId: res.data.userId, nickName: res.data.nickname },
              { path: "/", expires: new Date(res.data.cookie.expires) }
              // Dev:  , 9    
              // { path:'/', expires: after1 }
            );

            setTimeout(function () {
              navigate("/Main");
            }, 0);
            alert(res.data.nickname + " !");
          } else {
            alert(",  .");
          }
        })
        .catch((err) => {
          console.log(err);
          alert("    .");
        });
    }
  };

  const onClickSignUp = () => {
    navigate("/SignUp");
  };

  const onKeyDownInput = (e) => {
    if (e.keyCode === 13) {
      onClickButton();
    }
  };

  return (
    <div className="login-area">
      <div className="title-box">
        <img src={process.env.PUBLIC_URL + "/dongguk_logo.jpg"} alt="logo" />
        <div>
          <h1>Read Lead</h1>
          <h4> SW   </h4>
        </div>
      </div>
      <div className="login-box">
        <div className="flex-vertical">
          <div className="flex-horizontal text-fild">
            <div className="login-text flex-vertical">
              <TextField
                label="ID"
                onChange={onChangeId}
                onKeyDown={onKeyDownInput}
              />
              <TextField
                type="password"
                label="PW"
                onChange={onChangePw}
                onKeyDown={onKeyDownInput}
              />
            </div>
            <Button variant="contained" color="primary" onClick={onClickButton}>
              Login
            </Button>
          </div>

          <div className="flex-horizontal signup-fild">
            <GroupAddIcon />
            <label onClick={onClickSignUp}></label>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SignIn;
